footer {
	background: $foreground-primary;
	padding: 50px 0 0;

	@include media-breakpoint-down(xs) {
		padding: 50px 0 0;
	}

	//pre-footer
	.pre-footer {
		padding: 0 0 50px;
		border-bottom: 1px solid #fff;
		text-align: center;
		margin-bottom: 50px;

		form {
			display: flex;
			justify-content: center;
			align-items: center;

			@include media-breakpoint-down(xs) {
				flex-direction: column;
			}

			h3 {
				display: inline-block;
				color: #fff;
				margin-right: 20px;
				font-weight: 500;
				margin-top: 0;
				margin-bottom: 0;
			}

			input[type="email"] {
				padding: 20px;
				border-radius: 500px;
				border: 1px solid #ddd;
				background: rgba(255,255,255,1);
				margin-top: 10px;
				width: 300px;
				line-height: 20px;
				font-size: 14px;
				margin-right: 20px;

				&:focus {
					background: rgba(255,255,255,1);
				}

				@include media-breakpoint-down(sm) {
					width: 100%;
					max-width: 100%;
					margin-right: 0;
				}
			}

			input[type="submit"] {
				background: transparent;
				color: #fff;
				border: 2px solid #fff;
				border-radius: 500px;
				text-align: center;
				padding: 10px 20px;
				font-family: "Sofia Pro", sans-serif;
				text-transform: uppercase;
				font-weight: 600;
				cursor: pointer;
				transition: all ease-in-out .2s;
				margin-top: 10px;
				line-height: 20px;

				&:hover, &:focus {
					background: #fff;
					color: $foreground-primary;
					transition: all ease-in-out .2s;
				}

				@include media-breakpoint-down(sm) {
					width: 100%;
				}
			}
		}
	}
	
	//Back to top
	.back-to-top {
		width: 60px;
		height: 60px;
		border: 2px solid #fff;
		padding: 20px;
		border-radius: 100%;
		transition: all ease-in-out .3s;

		.icon--arrow {
			transform: rotate(-90deg);
			width: 15px;
			height: 15px;
			position: relative;
			top: -7px;
			left: 1px;
			fill: #fff;
		}

		&:hover, &:focus {
					
			.icon--arrow {
				-webkit-animation: icon-bounce-top 1s infinite; /* Safari 4+ */
				-moz-animation:    icon-bounce-top 1s infinite; /* Fx 5+ */
				-o-animation:      icon-bounce-top 1s infinite; /* Opera 12+ */
				animation:         icon-bounce-top 1s infinite; /* IE 10+, Fx 29+ */
			}
		}
	}

	//Footer Menu
	.footer-menu {
		display: flex;

		@include media-breakpoint-down(xs) {
			margin-top: 30px;
		}

		.left {
			width: 50%;
		}

		.right {
			width: 50%;
		}

		a {
			display: block;
			color: #fff;
			font-size: 16px;
			font-weight: $font-medium;
			letter-spacing: 2px;
			padding: 20px 0;

			@include media-breakpoint-down(xs) {
				font-size: 14px;
				padding: 5px 0;
				letter-spacing: 0.5px;
			}
		}
	}

	//payoff
	.payoff {
		height: 19px;
		width: 219px;
		fill: #fff;
		display: block;
	}

	//Logo Alfasigma
	.logo-alfasigma {

		@include media-breakpoint-down(xs) {
			display: none;
		}

		img {
			max-width: 100%;
		}
	}

	.logo-alfasigma-mobile {
		display: none;

		@include media-breakpoint-down(xs) {
			display: block;
		}
	}

	//Social Footer
	.social-footer {
		margin-top: 20px;
		margin-bottom: 40px;

		ul {
			list-style: none;
			padding: 0;
			margin: 0;

			li {
				display: inline-block;
				padding: 0 30px;

				&:first-child {
					padding-left: 0;
				}

				@include media-breakpoint-down(lg) {
					padding: 0 10px;
				}

				@include media-breakpoint-down(xs) {
					padding: 0 10px;
				}
			}
		}

		.icon--instagram,
		.icon--facebook,
		.icon--linkedin {
			width: 18px;
			height: 18px;
			fill: #fff;
		}

		.icon--youtube {
			width: 24px;
			height: 18px;
			fill: #fff;
		}
	}

	//Newsletter Footer
	.nl-footer {

		h3 {
			color: #fff;
			font-size: 18px;
			font-weight: 500;
			margin: 0;
		}

		form {
			color: #fff;
			font-size: 14px;
			font-weight: 400;

			label {
				display: block;
				padding-top: 10px;
				padding-bottom: 5px;
			}

			input[type="email"] {
				padding: 10px;
				border-radius: 500px;
				border: 1px solid #ccc;
				margin-top: 10px;
				max-width: 200px;
				line-height: 20px;
			}

			input[type="submit"] {
				background: transparent;
				color: #fff;
				border: 2px solid #fff;
				border-radius: 500px;
				text-align: center;
				padding: 10px 20px;
				font-family: "Sofia Pro", sans-serif;
				text-transform: uppercase;
				font-weight: 600;
				cursor: pointer;
				transition: all ease-in-out .2s;
				margin-top: 10px;
				line-height: 20px;

				&:hover, &:focus {
					background: #fff;
					color: $foreground-primary;
					transition: all ease-in-out .2s;
				}
			}
		}
	}

	//Copy Footer
	.copy-footer {
		padding: 30px 0;
		border-top: 1px solid #fff;
		margin-top: 100px;

		@include media-breakpoint-down(xs) {
			margin-top: 50px;
		}

		.wrap {
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			
			.left {
				width: 50%;

				@include media-breakpoint-down(xs) {
					width: 100%;
				}

				.copy-menu {

					ul {
						list-style: none;
						padding: 0;
						margin: 0;

						li {
							display: inline-block;
							padding: 0 20px;

							&:first-child {
								padding-left: 0;
							}

							@include media-breakpoint-down(xs) {
								padding: 0;
								padding-right: 10px;
							}

							a {
								color: #fff;
								font-size: 12px;
								font-weight: $font-semibold;
								letter-spacing: 1px;
							}
						}
					}
				}
			}
			
			.right {
				width: 50%;

				@include media-breakpoint-down(xs) {
					width: 100%;
				}
				
				p {
					text-align: right;
					color: #fff;
					font-size: 12px;
					font-weight: $font-medium;
					letter-spacing: 1px;

					@include media-breakpoint-down(xs) {
						text-align: left;
					}
				}
			}
		}
	}

	//Cols

	@include media-breakpoint-up(sm) {}

	@include media-breakpoint-down(xs) {}
}

@-webkit-keyframes icon-bounce-top {
	0%   { transform: translateY(0) rotate(-90deg); }
	100% { transform: translateY(-5px) rotate(-90deg); }
}
@-moz-keyframes icon-bounce-top {
	0%   { transform: translateY(0) rotate(-90deg); }
	100% { transform: translateY(-5px) rotate(-90deg); }
}
@-o-keyframes icon-bounce-top {
	0%   { transform: translateY(0) rotate(-90deg); }
	100% { transform: translateY(-5px) rotate(-90deg); }
}
@keyframes icon-bounce-top {
	0%   { transform: translateY(0) rotate(-90deg); }
	100% { transform: translateY(-5px) rotate(-90deg); }
}