// Home Hero
.section--home-hero {

	.container {
		display: flex;
		flex-direction: column;
		justify-content: center;
		min-height: 80vh;
	}

	.title {
		text-align: center;
		font-size: 60px;
		font-weight: $font-semibold;
		line-height: 1.2;
		color: #000;
		margin-bottom: 30px;

		@include media-breakpoint-down(sm) {
			font-size: 50px;
			margin-bottom: 30px;
		}

		@media (max-width:736px) {
			font-size: 28px;
			margin-bottom: 30px;
		}

		br  {

			@include media-breakpoint-down(sm) {
				content: ' '
			}
		}

		br:after {
			@include media-breakpoint-down(sm) {
				content: ' '
			}
		}

		span {
			color: $foreground-primary;
		}
	}

	.img {
		text-align: center;

		img {
			max-width: 100%;
		}
	}

	.abstract {
		text-align: center;
		font-size: 36px;
		font-weight: $font-medium;
		line-height: 1.2;
		color: $foreground-secondary;
		margin-bottom: 50px;

		@include media-breakpoint-down(sm) {
			font-size: 20px;
			margin-bottom: 20px;
		}
	}

	.description {
		text-align: center;
		font-size: 18px;
		line-height: 32px;
		max-width: 660px;
		margin: 0 auto;
		font-weight: $font-medium;
		color: #121212;
		margin-top: 40px;

		@include media-breakpoint-down(sm) {
			font-size: 16px;
			margin-bottom: 20px;
		}
	}

	.cta {
		display: flex;
		justify-content: center;
		margin-top: 50px;

		.icon--arrow-circle-right { 
			width: 50px; 
			height: 50px;
			margin-right: 20px;
		}

		a {
			color: $foreground-primary;
			text-transform: uppercase;
			font-weight: $font-semibold;
			font-size: 14px;
			letter-spacing: 0.5px;
			display: flex;
			align-items: center;
			transition: all ease-in-out .3s;

			&.link-arrow {
				width: 60px;
				height: 60px;
				border: 2px solid;
				padding: 20px;
				border-radius: 100%;
				margin-right: 20px;
				transition: all ease-in-out .3s;

				.icon--arrow {
					fill: $foreground-primary;
				}
				
				&:hover, &:focus {
					
					.icon--arrow {
						-webkit-animation: icon-bounce 1s infinite; /* Safari 4+ */
						-moz-animation:    icon-bounce 1s infinite; /* Fx 5+ */
						-o-animation:      icon-bounce 1s infinite; /* Opera 12+ */
						animation:         icon-bounce 1s infinite; /* IE 10+, Fx 29+ */
					}
				}
			}
		}
	}
}

//Home Slide Prodotti
.section--home-slide-prodotti {
	padding: 100px 0;
	background-color: #efefef;

	@include media-breakpoint-down(xs) {
		padding: 50px 0;
	}

	.intro {
		display: flex;
		flex-direction: column;
		justify-content: center;
		text-align: center;

		.abstract {
			font-size: 14px;
			text-transform: uppercase;
			color: $foreground-primary;
			letter-spacing: 0.5px;
			line-height: 20px;
			font-weight: $font-semibold;
			margin-bottom: 30px;
		}

		.title {
			font-weight: $font-semibold;
			font-size: 36px;
			line-height: 54px;
			color: $foreground-secondary;

			@include media-breakpoint-down(xs) {
				font-size: 28px;
				line-height: 36px;
				margin-bottom: 20px;
			}
		}
	}

	.slide {
		padding: 100px 0 0;

		@include media-breakpoint-down(xs) {
			padding: 50px 0;
		}

		.slick-list {
			padding: 0 500px !important;

			@media (max-width: 1440px) {
				padding: 0 !important;
			}
		}

		.item {
			position: relative;
			text-align: center;
			min-height: 640px;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;

			@include media-breakpoint-down(md) {
				min-height: 460px;
			}

			@include media-breakpoint-down(sm) {
				min-height: 350px;
			}

			&:hover, &:focus {
				outline: none;
			}

			&.item-bottom {

				&:hover {

					img {

						@include media-breakpoint-up(sm) {
							-webkit-animation: myscale .5s;
							animation: myscale .5s;
							animation-fill-mode: forwards;
						}
					}
				}			
			}

			&.item-top {

				&:hover {
	
					.img {
						@include media-breakpoint-up(sm) {
							-webkit-animation: mymove .5s;
							animation: mymove .5s;
							animation-fill-mode: forwards;
						}

						img {
							animation: none
						}
					}
	
					.caption {
	
						@include media-breakpoint-up(sm) {
							text-align: left;
							padding-left: 100px;
						}
					}
				}
			}

			.img {
				position: absolute;
				z-index: 3;
				top: 0;

				img {
					max-width: 100%;

					@include media-breakpoint-down(md) {
						max-width: 370px;
					}
					
					@media (max-width: 320px) {
						max-width: 280px;
					}
				}

				
				&--top {
					position: absolute;

					img {

						@include media-breakpoint-down(md) {
							max-height: 420px;
						}

						@include media-breakpoint-down(sm) {
							max-height: 320px;
						}
					}
				}
			}

			.caption {

				@include media-breakpoint-down(xs) {
					position: relative;
					top: -40px;
				}

				.title {

					@include media-breakpoint-down(sm) {
						margin-top: 0;
					}
				}
			}

			.title {
				color: $foreground-primary;
				font-weight: 500;
				font-size: 60px;
				letter-spacing: 0.2px;
				line-height: 64px;
				z-index: 2;
				position: relative;

				@include media-breakpoint-down(md) {
					font-size: 40px;
					margin-top: 30px;
				}

				@include media-breakpoint-down(sm) {
					font-size: 24px;
				}
			}

			.description {
				color: $foreground-secondary;
				font-size: 40px;
				font-weight: $font-regular;
				line-height: 44px;
				letter-spacing: 0.2px;
				z-index: 2;
				position: relative;
				margin: 20px 0 40px;

				@include media-breakpoint-down(md) {
					font-size: 32px;
					line-height: 36px;
				}

				@include media-breakpoint-down(sm) {
					font-size: 20px;
					line-height: 24px;
					margin: 20px 0;
				}
			}

			.cta {
				position: relative;
				z-index: 4;

				a {
					color: $foreground-primary;
					text-transform: uppercase;
					font-size: 16px;
					line-height: 20px;
					letter-spacing: 0.5px;
					font-weight: $font-semibold;
				}
			}
		}
	}
}

