//Home Testimonial
.section--home-testimonial {
	padding: 100px 0;

	.slide {
		background: $foreground-primary;
		margin-left: 300px;
		padding: 50px 0;
		box-shadow: 110px 0px 0px rgba(255,255,255,1) inset;

		@include media-breakpoint-down(xs) {
			margin-left: 0;
			box-shadow: none;
		}

		.item {
			
			&:hover, &:focus {
				outline: none;
			}
		}

		.slick-list {
			overflow: visible !important;
		}

		.slick-dots {

			li {

				&.slick-active {

					button {

						&:after {
							border: 2px solid #fff !important;
							background: transparent !important;
						}
					}
				}
			}
		}

		.row {
			align-items: center;
		}

		img {
			max-width: 100%;
		}

		.quote {
			color: #fff;
			font-size: 24px;
			line-height: 40px;
			margin-bottom: 20px;
			font-weight: $font-light;

			@include media-breakpoint-down(xs) {
				font-size: 18px;
				line-height: 28px;
				padding: 0 20px;
			}

			span {
				font-size: 32px;
				font-style: italic;
				font-weight: $font-semibold;
				position: absolute;

				@include media-breakpoint-down(xs) {
					font-size: 20px;
				}

				&:first-child {
					left: -10px;

					@include media-breakpoint-down(xs) {
						right: 0;
					}
				}
			}
		}

		.quote-nome {
			color: #fff;
			font-size: 14px;
			font-weight: $font-semibold;
			text-transform: uppercase;

			@include media-breakpoint-down(sm) {
				padding: 20px;
			}
		}
	}
}

